@import "./common.scss";

.App {
  text-align: center;
  height: 100vh;

  overflow-y: hidden;

  .footer {
    background-color: $arcta-color;
    padding: 0.4rem;
    color: white;
    width: 100vw;
    position: fixed;
    font-size: 1.5rem;
    font-family: GlacialIndifference;
    bottom: 0;

    a {
      color: white;
    }
  }
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
