@import "./common.scss";

.landing {
  height: 100%;
  width: 100%;
  align-items: stretch;
  justify-content: center;

  font-family: GlacialIndifference;

  display: flex;
  flex-direction: column;

  .title {
    padding: 0;
    margin-top: -3rem;
    font-size: 4rem;
    letter-spacing: 20px;
    color: $arcta-color;

    @media only screen and (max-width: 550px) {
      margin-top: -10vw;
      font-size: 10vw;
    }
  }

  .top {
    animation: fadeInFromTop 1.5s ease-out;

    .logo {
      width: 23rem;

      @media only screen and (max-width: 550px) {
        width: 70vw;
      }
    }
  }

  .below {
    animation: fadeInFromBelow 1.5s ease-out;
    display: flex;
    flex-direction: row;
    justify-content: center;

    .slogan {
      width: 60vw;
      font-size: 2rem;
      text-align: center;

      @media only screen and (max-width: 550px) {
        font-size: 5vw;
        width: 60vw;
      }
    }

    .sub-slogan {
      width: 70vw;
      font-size: 3.5rem;
      text-align: center;
      letter-spacing: 5px;
      color: $arcta-color;

      @media only screen and (max-width: 550px) {
        font-size: 7vw;
        width: 60vw;
      }
    }
  }
}

@keyframes fadeInFromTop {
  0% {
    transform: translateY(-20%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInFromBelow {
  0% {
    transform: translateY(20%);
    opacity: 0;
  }
  100% {
    transform: translateY(0);
    opacity: 1;
  }
}